<template>
  <div class="teacher-class-score">
    <div class="teacher-class-header">
      <span class="title">三年(2)班 —— 成绩详情</span>
      <span class="back-btn" @click="back">返回</span>
    </div>
    <div class="teacher-class-sub-panel">
      <div class="head">
        <span
          class="data"
          :class="{'choosed':isChoosed==0}"
          @click="selectType='date';isChoosed=0"
        >日</span>
        <span
          class="data"
          :class="{'choosed':isChoosed==1}"
          @click="selectType='month';isChoosed=1"
        >月</span>
        <span
          class="data"
          :class="{'choosed':isChoosed==2}"
          @click="selectType='year';isChoosed=2"
        >年</span>
        <el-date-picker
          class="choose-data"
          v-model="selectDate"
          :type="selectType"
          :clearable="false"
        ></el-date-picker>
        <!-- <span class="icon">
          <i class="iconfont icon-fangkuai red-bg"></i>&nbsp;
          <span class="text">未通过</span>
        </span>
        <span class="icon">
          <i class="iconfont icon-fangkuai green-bg"></i>&nbsp;
          <span class="text">通过</span>
        </span> -->
      </div>
      <div class="chart-box">
        <div id="scoreChart" style="width:100%;height:100%"></div>
      </div>
      <div class="choose-subject">
        <el-dropdown class="subject-dropdown" @command="handleCommand">
          <span class="el-dropdown-link">
            {{subject}}
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item
              v-for="(item ,index) in subjectList"
              :key="index"
              :command="index"
            >{{item.text}}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <el-table :data="tableData" style="width: 100%" class="score-table">
        <el-table-column label="课程名称">
          <template slot-scope="scope">
            <span class="subject-link blue-text" @click="showDetail(scope.row.id)">{{scope.row.name}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="date" label="验证日期"></el-table-column>
        <el-table-column prop="total" label="参加人数"></el-table-column>
        <el-table-column prop="pass" label="通过人数"></el-table-column>
        <el-table-column prop="fail" label="不通过人数"></el-table-column>
        <el-table-column prop="rate" label="通过率"></el-table-column>
      </el-table>
      <div class="course-page" style="margin-top:36px">
        <el-pagination layout="prev, pager, next" :page-size="10" :total="allItems"></el-pagination>
      </div>
    </div>
    <!-- 学科详情 -->
    <el-dialog
      class="subject-dialog"
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      width="36%"
    >
      <el-table
        :data="subjectTable"
        border=""
        height="400px"
        style="width: 100%"
        class="score-table"
        stripe
      >
        <el-table-column prop="name" label="姓名"></el-table-column>
        <el-table-column prop="gender" label="性别"></el-table-column>
        <el-table-column prop="number" label="学号"></el-table-column>
        <el-table-column label="测试结果">
          <template slot-scope="scope">
            <span
              class="subject-result"
              :class="{'green-bg':scope.row.result == '通过','red-bg':scope.row.result == '未通过'}"
            >{{scope.row.result}}</span>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>
<script>

import "echarts/lib/chart/pie";
import "echarts/lib/component/tooltip";
let echarts = require("echarts/lib/echarts");
export default {
  data() {
    return {
      selectType: "date",
      isChoosed: 0,
      allItems: 20,
      selectDate: new Date(),
      subject: "语文",
      subjectList: [
        {
          cmd: "",
          text: "语文"
        },
        {
          cmd: "",
          text: "数学"
        },
        {
          cmd: "",
          text: "英语"
        }
      ],
      tableData: [
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        },
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        },
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        },
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        },
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        },
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        },
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        },
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        },
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        },
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        },
        {
          id: "123",
          name: "语文",
          date: "2018-11-01",
          total: 52,
          pass: 41,
          fail: 11,
          rate: "78.8%"
        }
      ],
      dialogTitle: "2018-11-01 语文测验",
      dialogVisible: false,
      subjectTable: [
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "通过"
        },
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "未通过"
        },
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "未通过"
        },
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "未通过"
        },
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "通过"
        },
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "通过"
        },
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "未通过"
        },
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "通过"
        },
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "通过"
        },
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "未通过"
        },
        {
          name: "李建国",
          gender: "男",
          number: "233333333",
          result: "通过"
        }
      ]
    };
  },
  mounted() {
    this.drawScoreChart();
  },
  methods: {
    back() {
      this.$router.back();
    },
    handleCommand(index) {
      this.subject = this.subjectList[index].text;
    },
    drawScoreChart() {
      let option = {
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c}人",
          textStyle:{
            fontSize: 22
          }
        },
        legend: {
          show: true,
          right: 0
        },
        color: ["#EEB00A", "#3FABFF"],
        series: [
          {
            type: "pie",
            radius: "69%",
            center: ["50%", "50%"],
            data: [{ value: 12, name: "未通过" }, { value: 40, name: "通过" }],
            label: {
              formatter: "{d}%\n {hr|}\n {rateText|{b}率}",
              textStyle: {
                fontSize: 18,
                color: "#666"
              },
              rich: {
                hr: {
                  borderColor: "#666",
                  width: "100%",
                  borderWidth: 0.5,
                  height: 0
                },
                rateText: {
                  align: "left",
                  color: "#666"
                }
              }
            },
            labelLine: {
              length: 22,
              length2: 36,
              lineStyle: {
                color: "#666"
              }
            },
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      let chart = echarts.init(document.getElementById("scoreChart"));
      chart.setOption(option);
    },
    showDetail(id) {
      console.log(id);
      this.dialogVisible = true;
    }
  }
};
</script>
<style lang="less">
.teacher-class-score {
  width: 100%;
  .teacher-class-sub-panel {
    .choose-subject {
      width: 100%;
      .subject-dropdown {
        font-size: 1rem !important;
        font-weight: 600;
        &:hover {
          cursor: pointer;
        }
      }
    }
    .score-table {
      margin-top: 22px;
      border-top: 1px dotted #666;
      .subject-link:hover {
          cursor: pointer;
      }
    }
  }
  .subject-dialog {
    .el-dialog__header {
      text-align: left;
    }
    .subject-result {
      padding: 1px 3px 2px;
      color: #fff;
      border-radius: 3px;
    }
  }
}
</style>



